import { Component, OnInit } from '@angular/core';
import { Transactions } from 'src/app/Models/Transactions';
import { ViewaccountstatementService } from 'src/app/services/viewaccountstatement.service';

@Component({
  selector: 'app-accountstatement',
  templateUrl: './accountstatement.component.html',
  styleUrls: ['./accountstatement.component.css']
})
export class AccountstatementComponent implements OnInit {

  transactions:Transactions
  accountNo:number=3549777
  fromDate:String="29-Dec-2018"
  toDate:String="31-Dec-2019"

  constructor(private httpclientservice:ViewaccountstatementService) { }

  viewAccountStatement(accountNo:number, fromDate:String, toDate:String){

    this.accountNo = accountNo;
    this.fromDate= fromDate
    this.toDate=toDate
    return this.httpclientservice.getAccountStatement(this.accountNo,this.fromDate,this.toDate  ).
    subscribe(acctStatement=>{this.transactions=<Transactions>acctStatement;})
    
  }



  ngOnInit() {
    this.viewAccountStatement(this.accountNo, this.fromDate, this.toDate)
  }

}
