import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { AccountsummaryComponent } from './components/accountsummary/accountsummary.component';
import { AccountprofileComponent } from './components/accountprofile/accountprofile.component';
import { AccountstatementComponent } from './components/accountstatement/accountstatement.component';
import { BeneficiarydetailsComponent } from './components/beneficiarydetails/beneficiarydetails.component';
import { AddbeneficiaryComponent } from './components/addbeneficiary/addbeneficiary.component';

const routes: Routes = [
  {path :'home', component:HomeComponent},
  {path :'accountsummary', component:AccountsummaryComponent},
  {path :'viewprofile', component:AccountprofileComponent},
  {path :'accountstatement', component:AccountstatementComponent},
  {path :'beneficiarydetails', component:BeneficiarydetailsComponent},
  {path :'addbeneficiary', component:AddbeneficiaryComponent},
  {path :'**', component:HomeComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
