import { Component, OnInit } from '@angular/core';
import { ViewprofileService } from 'src/app/services/viewprofile.service';
import { CustomerDetails } from 'src/app/Models/CustomerDetails';

@Component({
  selector: 'app-accountprofile',
  templateUrl: './accountprofile.component.html',
  styleUrls: ['./accountprofile.component.css']
})
export class AccountprofileComponent implements OnInit {

  customerDetails:CustomerDetails
  customerId:number=3843000

  constructor(private httpclientservice:ViewprofileService) { }

  viewCustomerProfile(customerId:number){
    this.customerId = customerId;
    return this.httpclientservice.getCustomerProfile(this.customerId).subscribe(custProfile=>{this.customerDetails=<CustomerDetails>custProfile;})
  }

  ngOnInit() {
    this.viewCustomerProfile(this.customerId)
  }

}
