import { Component, OnInit } from '@angular/core';
import { ViewbalanceService } from 'src/app/services/viewbalance.service';
import { Accounts } from 'src/app/Models/Accounts';

@Component({
  selector: 'app-accountsummary',
  templateUrl: './accountsummary.component.html',
  styleUrls: ['./accountsummary.component.css']
})
export class AccountsummaryComponent implements OnInit {
  account: Accounts
  accountNos:number=3549777
  constructor(private httpclientservice:ViewbalanceService) { }

  
  ngOnInit() {
    this.getAccountSummary(this.accountNos)
    
  }

  getAccountSummary(accountNos:number){
    this.accountNos = accountNos;
    return this.httpclientservice.getAccountBalance(this.accountNos).subscribe(acct=>{this.account=<Accounts>acct;})
  }

  getdata(){
    console.log(this.account)
  }

  
 



}
