import { Component, OnInit } from '@angular/core';
import { BeneficiaryDetails } from 'src/app/Models/BeneficiaryDetails';
import { ViewbeneficiarylistService } from 'src/app/services/viewbeneficiarylist.service';

@Component({
  selector: 'app-beneficiarydetails',
  templateUrl: './beneficiarydetails.component.html',
  styleUrls: ['./beneficiarydetails.component.css']
})
export class BeneficiarydetailsComponent implements OnInit {
  benficiaryList:BeneficiaryDetails
  customerId:number=3844350

  constructor(private httpclientservice:ViewbeneficiarylistService) { }

  getBeneficiaryDetailedList(customerId:number){
    this.customerId=customerId

    //return this.httpclientservice.getBeneficicaryList(this.customerId).subscribe(beneList=>{this.benficiaryList=<BeneficiaryDetails>beneList;})
    return this.httpclientservice.getBeneficicaryList(this.customerId).subscribe(beneList=>{this.benficiaryList=<BeneficiaryDetails>beneList;})
    //return this.httpclientservice.getAccountBalance(this.accountNos).subscribe(acct=>{this.account=<Accounts>acct;})
    
    
  }


  ngOnInit() {    
    this.getBeneficiaryDetailedList(this.customerId)
  }

}
