import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CustomerDetails } from 'src/app/Models/CustomerDetails';
import { BeneficiaryDetails } from 'src/app/Models/BeneficiaryDetails';
import { AddbeneficiaryService } from 'src/app/services/addbeneficiary.service';


@Component({
  selector: 'app-addbeneficiary',
  templateUrl: './addbeneficiary.component.html',
  styleUrls: ['./addbeneficiary.component.css']
})

export class AddbeneficiaryComponent implements OnInit {
beneDetailsToAdd: BeneficiaryDetails[]=[]
beneficiaryIfscCode:String;
beneficiartName:String;
beneficiaryAccountNo:number;
customerDetail:CustomerDetails

  constructor(private addbeneservice:AddbeneficiaryService) { }

  ngOnInit() {
   
  }

  addNewBeneficiary(frm:NgForm): void{ 

    this.customerDetail=(frm.value.custid);
    this.beneficiaryIfscCode=(frm.value.beneifsccode);
    this.beneficiartName=(frm.value.benename);
    this.beneficiaryAccountNo=(frm.value.beneacctno);

    console.log(this.customerDetail);
    console.log(this.beneficiaryIfscCode);
    console.log(this.beneficiartName);
    console.log(this.beneficiaryAccountNo);

    let newBeneDetails = new BeneficiaryDetails(this.beneficiaryIfscCode,this.beneficiartName,
      this.customerDetail,this.beneficiaryAccountNo);

      this.addbeneservice.addNewBeneficiary(newBeneDetails).subscribe(
        beneDetailsItem =>{
          console.log("POST Request is successful ", beneDetailsItem);
      },
      error => {
          console.log("Error", error);
      })

  }
  
    
}
